import { useEffect, useRef } from "react";
import { isIntoView } from "../../../utils";

import * as styled from "./Documentation.styled";

export type DocumentationProps = {
  title: string;
  src: string;
  paragraphs?: string[];
};

export const Documentation: React.VFC<DocumentationProps> = ({ title, src, paragraphs }) => {
  const video = useRef<HTMLVideoElement>(null);

  useEffect(() => {
    if (!video?.current) return;
    const { current: player } = video;

    const onScroll = () => {
      const play = isIntoView(player);
      const { paused } = player;

      play ? paused && player.play() : !paused && player.pause();
    };

    window.addEventListener("scroll", onScroll);

    return () => {
      window.removeEventListener("scroll", onScroll);
    };
  }, [video]);

  return (
    <styled.BodyWrapper>
      <styled.Title>{title}</styled.Title>
      {paragraphs && (
        <styled.NumberList>
          {paragraphs?.map((value) => (
            <styled.Paragraph key={value} children={value} />
          ))}
        </styled.NumberList>
      )}
      <styled.Video ref={video} src={src} playsInline loop muted controls={false} />
    </styled.BodyWrapper>
  );
};
