import * as styled from './Button.styled';

type Props = {
	link?: string;
	onClick?: () => void;
	filled?: boolean;
};

export const Button: React.FC<Props> = ({ children, link, filled, onClick }) => {
	return (
    <styled.BodyWrapper data-filled={filled} onClick={onClick}>
      {link ? <a href={link}>{children}</a> : <button>{children}</button>}
    </styled.BodyWrapper>
  );
};
